import { useState } from "react";
import styles from "../styles/NavBar.css";

const NavBar = () => {
  const [openDropdown, setOpenDropdown] = useState("");

  const handleDropdown = (section) => {
    setOpenDropdown(openDropdown === section ? "" : section);
  };

  return (
    <nav className={styles.navBar}>
      <ul>
        <li>
          <button onClick={() => handleDropdown("filtros")}>Filtros</button>
          {openDropdown === "filtros" && (
            <ul className={styles.dropdown}>
              <li>Ordenar por Preço: Baixo para Alto</li>
              <li>Ordenar por Preço: Alto para Baixo</li>
            </ul>
          )}
        </li>
        <li>
          <button onClick={() => handleDropdown("categorias")}>
            Categorias
          </button>
          {openDropdown === "categorias" && (
            <ul className={styles.dropdown}>
              <li>Com Carne</li>
              <li>Vegetariano</li>
              <li>Vegano</li>
            </ul>
          )}
        </li>
        <li>
          <button onClick={() => handleDropdown("tags")}>Tags</button>
          {openDropdown === "tags" && (
            <ul className={styles.dropdown}>
              <li>Saudável</li>
              <li>Fast Food</li>
              <li>Refeição Leve</li>
            </ul>
          )}
        </li>
        <li>
          <button onClick={() => handleDropdown("produtos")}>Produtos</button>
          {openDropdown === "produtos" && (
            <ul className={styles.dropdown}>
              <li>Produto 1</li>
              <li>Produto 2</li>
              <li>Produto 3</li>
            </ul>
          )}
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
